
import { defineComponent, computed, ref, onMounted, SetupContext } from "vue";
import { IMessageTemplate, IOrder, ISignup, ISubscription } from '@/types/Interfaces';
import useSubscription from "@/types/Subscription";

export default defineComponent({
  components: {
    
  },
  props: {
    subscription: {
      type: Object as () => ISubscription[]
    }
  },
  emits: [
    'close',
  ],
  setup(props: any, context: SetupContext) {
    const {
      getSubscription
    } = useSubscription('doctor');

    const subscStatus = {
      0: '',
      1: '',
      2: ''
    }
    const payStatus = {
      0: '',
      1: ' ',
      2: ''
    }

    const payMethods = {
      0: "",
      1: "",
      2: "Paidy",
      3: "NP",
      4: ""
    }
    //  const subscStatus = {
    //   0: '',
    //   1: '',
    //   2: ''
    // }
    const delivTimes = {
      0: "",
      1: "",
      12: "12:0014:00",
      14: "14:0016:00",
      16: "16:0018:00",
      18: "18:0020:00",
      19: "19:0021:00",
    }
    const orderStatus = {
      0: "",
      1: "",
      2: "",
      3: "",
      4: "",
      5: "",
    }
    const onClose = () => {
      context.emit('close')  
    };
    const data = ref<ISubscription | null>(null);
    const userId = computed(() => {
      return props.prescript.doctor.id
    })
    const loading = ref(true);
    onMounted(async () => {
      // loading.value = true;
      data.value = await getSubscription(props.subscription.id);
      loading.value = false
    })
    return {
      onClose,
      userId,
      payStatus,
      subscStatus,
      payMethods,
      delivTimes,
      orderStatus,
      data,
      loading
    }
  }
})
